import React from 'react';
import ReactDOM from 'react-dom';
import 'antd-mobile/dist/antd-mobile.css';
import './index.css';
// import App from './App';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import IndexPages from './components/IndexPages';
import DetailsPages from './components/DetailsPages';
require('es6-promise').polyfill();

const  goHome = (props)=>{
        console.log(props)
        setTimeout(()=>{props.history.replace("/")},1000)
       return <div></div>
}

ReactDOM.render(
    <Router basename='/leonway.github.io/douban-pocket'>
        <Switch>
          <Route exact path="/" component={IndexPages}/>
          <Route exact path="/:aid" component={IndexPages}/>
          <Route exact path="/:aid/:bid" component={DetailsPages}/>
          {/* <Route component={goHome}/> */}
        </Switch>
    </Router>,
     document.getElementById('root'));
registerServiceWorker();



