import React, { Component } from 'react';
import { SearchBar } from 'antd-mobile';

class App extends Component {
  state = {
    value: '',
  };
  constructor({searchType}){
    super();
    this.state = { placeHolder : this.matchPlaceHolder(searchType) };
  }
  componentWillReceiveProps({searchType}){
    this.setState({placeHolder:this.matchPlaceHolder(searchType)});
  }
  componentDidMount() {
    this.autoFocusInst.focus();
  }

  matchPlaceHolder = (type) =>{
    switch(type)
    {
      case "book":{
       return "ISBN"
      }
      break;
      case "movie":{
       return ""
      }
      break;
      case "music":{
       return "ISRC"
      }
      break
      default:{
        return;
      }
      
    }
  }

  onChange= (value) => {
    const { searchType="book" } = this.props
    this.setState({ [`${searchType}value`]:value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  Submit = () => {
    const { getSearchData ,searchType="book"} = this.props;
    getSearchData&&getSearchData(this.state[`${searchType}value`])
  }
  render() {
   const { searchType="book" } = this.props;
    return (
      <div className="Search">
         {/* {this.props.placeHolder} */}
         <SearchBar
            ref={ref => this.autoFocusInst = ref}
            value={this.state[`${searchType}value`]||""}
            placeholder= {this.state.placeHolder}
            onSubmit={this.Submit}
            onClear={this.clear}
            onFocus={() => console.log('onFocus')}
            onBlur={() => console.log('onBlur')}
            cancelText=""
            onCancel={this.Submit}
            showCancelButton
            onChange={this.onChange}
        />
      </div>
    );
  }
}

export default App;


