import React, { Component } from 'react';
import { NavBar ,WingBlank } from 'antd-mobile';

class App extends Component {
      constructor(props){
        super(props);
      if(["book","movie","music"].indexOf(props.match.params.aid)==-1||["bookDetails","movieDetails","musicDetails"].indexOf(props.match.params.bid)==-1){
          return this.props.history.replace("/book");
      }
      this.state = {
          items:JSON.parse(sessionStorage.getItem('Details')),
          type:props.match.params.aid
      }
  }
    authors = (casts)=>{
            let realKeyWords =  casts.map(({name},i)=>{
                return(<span className="casts" key = {i}>{name}</span>)
            });
            realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
            return realKeyWords;

    };
    keyWordBook = (tags)=>{
        const { type } = this.props;
        let realKeyWords =  tags.map(({title,name},i)=>{
            if(title||name){
                return(<span className="tagWords tagWords-book" key = {i}>{title?title:name}</span>)
            }
        }).filter(x=>x);
        realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
        return realKeyWords;
    };
    keyWordMovie = (tags)=>{
        const { type } = this.props;
        let realKeyWords =  tags.map((item,i)=>{
            return(<span className="tagWords tagWords-movie" key = {i}>{item}</span>)
        }).filter(x=>x);
        realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
        return realKeyWords;
    };
    casts =(casts)=>{
         let contentCasts = casts.map(({avatars})=>(
             <img src={avatars.small} alt={avatars.alt} className="img-casts"/>
        ))
        contentCasts = contentCasts.length>3?contentCasts.slice(0,2):contentCasts;
         return contentCasts;
    };
    // https://api.douban.com/v2/book/:id
    // https://api.douban.com/v2/music/:id
// /v2/movie/subject/:id

      book = ({image,title,tags,author,rating,pubdate,publisher,price,catalog,summary})=>{
      return(
          <div>
              <div className = "row">
                      <img src={image} className="img-Details" />
                  <div>
                      <div className="name">{title}</div>
                      <div>{author}</div>
                      <div>{publisher}</div>
                      <div>{pubdate}</div>
                      <div>{rating.average}</div>
                      <div>{price}</div>
                      <div className="tags">
                          {tags.length&&this.keyWordBook(tags)}
                      </div>
                  </div>
              </div>
              <WingBlank size="md"><h3></h3><span className="text-Details"><span className="ident"></span>{catalog}</span></WingBlank>
              <WingBlank size="md"><h3></h3><span className="text-Details"><span className="ident"></span>{summary}</span></WingBlank>
          </div>

)
      };
      movie = ({images,title,genres,directors,casts,rating})=>{
              return(
                  <div>
                      <div style={{textAlign:"center"}}>
                          <img src={images.small} className="img-movie-Details" />
                      </div>
                      <WingBlank size="md">
                        <h3></h3>
                          <div className="row2">
                              <div className="name">{title}</div>
                              <div className="tags">
                                  {genres.length&&this.keyWordMovie(genres)}
                              </div>
                              <div>{directors?directors[0].name:""}</div>
                              <div>{rating.average}</div>
                          </div>
                      </WingBlank>
                      <WingBlank size="md"><h3></h3><div className="casts-box">{this.casts(casts)}</div></WingBlank>
                  </div>

              )
      };
      music = ({image,title,rating,author,alt,attrs,tags})=>{
          // book = ({image,title,tags,author,rating,pubdate,publisher,price,catalog,summary})=>{
              return(
                  <div>
                      <div className = "row">
                          <img src={image} alt={alt} className="img-Details" />
                          <div>
                              <div className="name">{title}</div>
                              <div>{author.length&&this.authors(author)}</div>
                              <div>{attrs.publisher}</div>
                              <div>{attrs.pubdate}</div>
                              <div>{rating.average}</div>
                              <div className="tags">
                                  {tags.length&&this.keyWordBook(tags)}
                              </div>
                          </div>
                      </div>
                      <WingBlank size="md"><h3></h3><span className="text-Details"><span className="ident"></span></span></WingBlank>
                      <WingBlank size="md"><h3></h3><span className="text-Details"><span className="ident"></span>{attrs.tracks}</span></WingBlank>
                  </div>

              )

      };
      goBack = (type)=>{
        window.location.replace(`/${type}`);
      }
    render() {
        const { items ,type } = this.state;
        return (
            <div>
                <NavBar mode="dark" leftContent="" onLeftClick={this.goBack.bind(null,type)}>{items.title}</NavBar>
              <div style={{position: 'fixed', height:"100%", width: '100%', top: "5rem" ,overflowY:"scroll"}}>
                  {this[type](items)}
              </div>

            </div>
        );
    }
}

export default App;




