import React, { Component } from 'react';
// import List from '../List';
import List2 from '../List2';
import Search from '../Search';
import TableBoor from '../TableBoor';
import { WingBlank, WhiteSpace } from 'antd-mobile';

class App extends Component {

    constructor(){
      super();
      this.state={ searchData:"a" }
  }
  getSearchProps = ({ params }) => {
    // console.log(params.aid);
    if(["book","movie","music"].indexOf(params.aid)==-1){
      return this.props.history.replace("/book");
    }
    return params.aid; 
  }

  render() {
    // console.log(this.props)
    const { match } = this.props;
    return (
      <div className="App">
      {/* <WingBlank ><Search searchType = { this.getSearchProps(match)} getSearchData = {this.getSearchData} /></WingBlank> */}
      {/* <WingBlank><List searchData = {this.state.searchData} listType = { match.params.aid } /></WingBlank>     */}
      {/* <WingBlank><List2 searchData = {this.state.searchData} listType = { match.params.aid } /></WingBlank>     */}
          <TableBoor listType = { this.getSearchProps(match)}/>
      </div>
    );
  }
}

export default App;


