import React, { Component } from 'react';
import { WhiteSpace } from "antd-mobile";


class App extends Component {
    jump = (type)=>{
        sessionStorage.setItem('Details', JSON.stringify(this.props.items));
        window.location.replace(`/${type}/${type}Details`)
    };
    casts = casts =>{
        let realKeyWords =  casts.map(({name},i)=>{
                return(<span className="casts" key = {i}>{name}</span>)
        });
        realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
        return realKeyWords; 
    };
    keyWordBook = (tags)=>{
        const { type } = this.props;
        let realKeyWords =  tags.map(({title},i)=>{
                if(title){
                    return(<span className="tagWords tagWords-book" key = {i}>{title}</span>)
                }
            }).filter(x=>x);
            realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
            return realKeyWords; 
    };
    keyWordMovie = (tags)=>{
        const { type } = this.props;
      let realKeyWords =  tags.map((item,i)=>{
                return(<span className="tagWords tagWords-movie" key = {i}>{item}</span>)
        }).filter(x=>x);
        realKeyWords = realKeyWords.length>3?realKeyWords.slice(0,2):realKeyWords;
        return realKeyWords; 
    };
    book = ({image,title,tags,author,rating,pubdate},type) =>{
        return(
            <dd className = "row" onClick={this.jump.bind(null,type)}>
                <img src={image} />
                <div>
                    <div className="name">{title}</div>
                    <div className="tags">
                        {tags.length&&this.keyWordBook(tags)}
                    </div>
                    <div>{author}</div>
                    <div>{rating.average}</div>
                    <div>{pubdate}</div>
                </div>
            </dd> 
        )
    };

    movie = ({images,title,genres,casts,rating,pubdate},type) =>{
        return(
            <dd className = "row" onClick={this.jump.bind(null,type)}>
                <img src={images.small} />
                <div>
                    <div className="name">{title}</div>
                    <div className="tags">
                        {genres.length&&this.keyWordMovie(genres)}
                    </div>
                    <div>{casts.length&&this.casts(casts)}</div>
                    <div>{rating.average}</div>
               </div>
            </dd> 
        )
    };

    music = ({image,title,rating,author},type) =>{
        return(
            <dd className = "row" onClick={this.jump.bind(null,type)}>
                <img src={image} />
                <div>
                    <div className="name">{title}</div>
                    <div>{author.length&&this.casts(author)}</div>
                    <div>{rating.average}</div>
               </div>
            </dd> 
        )
    };
  render() {
     const { type ,items} = this.props;
    return (
        <div>
           {this[type](items,type)}
           <WhiteSpace />
        </div>
    );
  }
}

export default App;


