/* eslint no-dupe-keys: 0, no-mixed-operators: 0 */
import { ListView, ActivityIndicator, PullToRefresh } from 'antd-mobile';
import React from 'react';
import ReactDOM from 'react-dom';
import fetchJsonp from 'fetch-jsonp';
import Item from "../Item";
// var async = require("async");

function MyBody(props) {
  return (
    <div className="am-list-body my-body">
      <span style={{ display: 'none' }}>you can custom body wrap element</span>
        {props.children}
    </div>
  );
}



class App extends React.Component {
  constructor(props ) {
    super(props);
    const { searchData, listType } = props;
    // const getSectionData = (dataBlob, sectionID) => dataBlob[sectionID];
    // const getRowData = (dataBlob, sectionID, rowID) => dataBlob[rowID];

    const dataSource = new ListView.DataSource({
      // getRowData,
      // getSectionHeaderData: getSectionData,
      rowHasChanged: (row1, row2) => row1 !== row2,
      // sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
    });

    this.state = {
      refreshing: false,
      useBodyScroll: false,
      dataSource,
      isLoading: true,
      height: (document.documentElement.clientHeight * 3) / 4,
    };
    // console.log("constructor",listType);
    this.getList(searchData, listType, true);
    // console.log("constructor",1)
  }

  componentWillReceiveProps({ searchData, listType }) {
    // console.log("componentWillReceiveProps",listType)
    // this.setState({ refreshing: true, isLoading: true });
    this.getList(searchData, listType, true);
    // console.log("componentWillReceiveProps",2)
    
  }

  componentDidMount() {
    // you can scroll to the specified position
    // setTimeout(() => this.lv.scrollTo(0, 120), 800);
      this.lv.listviewRef.ListViewRef.InnerScrollViewRef.style.maxWidth="100%";
    const hei = document.documentElement.clientHeight - ReactDOM.findDOMNode(this.lv).parentNode.offsetTop;
    // simulate initial Ajax
    // this.getList();
    this.setState({
      height: hei,
    });
  }

  /** 
   * 
   * */
  getList = async (searchData = "a", listType = "book", isInit) => {
    let pageIndex = isInit ? 1 : (this.state && this.state[`${listType}-pageIndex`] ? (this.state[`${listType}-pageIndex`] + 1) : 1);
    const url = `https://api.douban.com/v2/${listType}/search?q=${searchData}&count=10&start=${(pageIndex - 1) * 10}`;
    const { json: renderDetails, ok } = await fetchJsonp(url);
    if (ok) {
      const result = await renderDetails()
      let listDetails = result[`${listType}s`] ? result[`${listType}s`] : result.subjects
      listDetails = isInit ? listDetails : (this._data ? this._data.concat(listDetails) : listDetails);
      this._data = listDetails;
      // console.log(listDetails)
      this.setState({
        [`${listType}-pageIndex`]: pageIndex,
        dataSource: this.state.dataSource.cloneWithRows(listDetails),
        isLoading: false,
        refreshing: false,
      }, () => this.render());
      // this.setState({status:"complete",listDetails},()=>this.render())
    } else {
      console.log(await renderDetails())
    }
  }

  onRefresh = () => {
    this.setState({ refreshing: true,isLoading:false });
    // simulate initial Ajax
    const {listType,searchData} = this.props;
    this.getList(searchData,listType,true)

  };


  onEndReached = (event) => {
    // load new data
    // hasMore: from backend data, indicates whether it is the last page, here is false
    if (this.state.isLoading && !this.state.hasMore) {
      return;
    }
    // console.log('reach end', event);
    const { searchData, listType } = this.props;
    this.setState({ isLoading: true });
    this.getList(searchData, listType)
  }

  render() {
    // const separator = (sectionID, rowID) => (
    //   <div
    //     key={`${sectionID}-${rowID}`}
    //     style={{
    //       backgroundColor: '#F5F5F9',
    //       height: 8,
    //       borderTop: '1px solid #ECECED',
    //       borderBottom: '1px solid #ECECED',
    //     }}
    //   />
    // );
// console.log(this.props,this.state)
    const row = (rowData, sectionID, rowID) => {
      // console.log(rowData, sectionID, rowID,this.state);
      const { listType } = this.props;
      return (
        <Item className="lBox" type={listType} items={rowData} />
      );
    };

    return (
      <ListView
        ref={el => this.lv = el}
        dataSource={this.state.dataSource}
        renderFooter={() =>(<div style={{ padding: 30, textAlign: 'center' }}>
      {this.state.isLoading ? 'Loading...' : 'Loaded'}       </div>)}
        renderBodyComponent={() => <MyBody />}
        renderRow={row}
        // renderSeparator={separator}
        style={{
          height: this.state.height,
          overflow: 'auto',
        }}
        pageSize={4}
        onScroll={() => { console.log('scroll'); }}
        scrollRenderAheadDistance={500}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={10}
        useBodyScroll={this.state.useBodyScroll}
        pullToRefresh={<PullToRefresh
          refreshing={this.state.refreshing}
          onRefresh={this.onRefresh}
        />}
      />
    );
  }
}

export default App;


